"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import type { Product } from "sam-shared";
import { Button } from "@/components/ui/button";
import { columns } from "./columns";
import { DataTable } from "./data-table";
import axios from "axios";

// TODO: The user did not specify an endpoint for fetching products.
// Assuming GET /products for now.
async function getProducts(): Promise<Product[]> {
  try {
    const response = await axios.get("http://localhost:3000/products");
    return response.data;
  } catch (error) {
    console.error("Failed to fetch products:", error);
    // Returning empty array as a fallback
    return [];
  }
}

export default function ProductsPage() {
  const router = useRouter();
  const [data, setData] = useState<Product[]>([]);

  useEffect(() => {
    getProducts().then(setData);
  }, []);

  return (
    <div className="container mx-auto py-10">
      <div className="flex justify-between items-center mb-4">
        <h1 className="text-2xl font-bold">商品列表</h1>
        <Button onClick={() => router.push("/products/import")}>
          导入商品
        </Button>
      </div>
      <DataTable columns={columns} data={data} />
    </div>
  );
} 