"use client"

import { type ColumnDef } from "@tanstack/react-table"
import type { Product } from "sam-shared"

export const columns: ColumnDef<Product>[] = [
  {
    accessorKey: "spuId",
    header: "SPU ID",
  },
  {
    accessorKey: "image",
    header: "图片",
    cell: ({ row }) => {
        const image: string = row.getValue("image")
        const title: string = row.getValue("title")
        return <img src={image} alt={title} className="w-16 h-16 object-cover" />
    }
  },
  {
    accessorKey: "title",
    header: "标题",
  },
  {
    accessorKey: "priceInfo",
    header: "价格",
    cell: ({ row }) => {
        const priceInfo = row.getValue("priceInfo") as Product['priceInfo']
        return priceInfo?.[0]?.price ?? "N/A"
    }
  },
  {
    accessorKey: "stockInfo",
    header: "库存",
    cell: ({ row }) => {
        const stockInfo = row.getValue("stockInfo") as Product['stockInfo']
        return stockInfo?.stockQuantity ?? "N/A"
    }
  },
  {
    accessorKey: "isAvailable",
    header: "是否可用",
  },
] 